.slider__item {
  img {
    // width: 100%;
    margin: auto;
  }
}

.slick-list {
  overflow: hidden;
}

.slick-track {
  display: flex;
}

.slider {
  position: relative;
}

.slider .slick-arrow {
  position: absolute;
  top: 50%;
  z-index: 10;
  font-size: 0;
  width: 34px;
  height: 36px;
  border-color: #fff;
  border-radius: 3px;
  margin: 15px;
}

.slider .slick-arrow.slick-prev {
  left: 0;
  background: url(../img/arrow.png) 0 0 no-repeat;
  cursor: pointer;
  display: none;
}

.slider .slick-arrow.slick-next {
  right: 0;
  background: url(../img/arrow2.png) 0 0 no-repeat;
  cursor: pointer;
  display: none;
}


.slider .slick-dots {
  display: flex;
  position: absolute;
  right: 6%;
  bottom: 40px;
}

.slider .slick-dots li {
  list-style: none;
  margin: 0 7px;
}

.slider .slick-dots button {
  font-size: 0;
  width: 10px;
  height: 16px;
  background-color: #fff;
  border-color: transparent;

  border-radius: 50%;

  &:hover {
    background-color: #222;
    transform: scale(1.1)
  }
}

.slider .slick-dots li .slick-active button {
  background-color: #222;
  border: 4px solid #fff;
}